import React from "react";
import { RoleEditProps, RoleInput } from "src/types/role";
import { useFormik } from "formik";
import * as Yup from "yup";
import TextInput from "@components/input/TextInput";
import { CREATE_ROLE, UPDATE_ROLE } from "@framework/graphql/mutations/role";
import { useMutation } from "@apollo/client";
import { RoleCreateRes, UpdateRoleDataType } from "@framework/graphql/graphql";
import { toast } from "react-toastify";

const AddEditRole = ({
  isRoleModelShow,
  onSubmitRole,
  onClose,
  roleVal,
  isRoleEditable,
  roleObj,
}: RoleEditProps) => {
  const [updateRoleData] = useMutation(UPDATE_ROLE);
  const [createRoleData] = useMutation(CREATE_ROLE);

  //initial values
  const initialValues: RoleInput = {
    role: roleVal || "",
  };
  //validation
  const validationSchema = Yup.object({
    role: Yup.string().required("Please enter role" as string),
  });
  //submit form
  const formik = useFormik({
    initialValues,
    validationSchema,
    onSubmit: (values) => {
      if (isRoleEditable) {
        UpdateRoleDataVal(values);
      } else {
        createRole(values);
      }
    },
  });
  //to create role
  const createRole = (values: RoleInput) => {
    createRoleData({
      variables: {
        roleName: values.role,
        key: values.role,
      },
    })
      .then((res) => {
        const data = res.data as RoleCreateRes;

        if (data.createRole.meta.statusCode === 200) {
          toast.success(data.createRole.meta.message);
        } else {
          toast.error(data.createRole.meta.message);
        }
        onSubmitRole();
      })
      .catch(() => {
        toast.error("Failed to create role");
      });
  };
  //update role
  const UpdateRoleDataVal = (values: RoleInput) => {
    updateRoleData({
      variables: {
        updateRoleId: roleObj?.id,
        roleName: values.role,
        key: values.role,
      },
    })
      .then((res) => {
        const data = res.data as UpdateRoleDataType;
        if (data.updateRole.meta.statusCode === 200) {
          toast.success(data.updateRole.meta.message);
        } else {
          toast.error(data.updateRole.meta.message);
        }
        onSubmitRole();
      })
      .catch(() => {
        toast.error("Failed to update the role");
      });
  };

  return (
    <div
      id="defaultModal"
      tabIndex={-1}
      data-modal-show={true}
      aria-hidden="false"
      className={
        "fixed top-0 left- right-0 z-50  w-full p-4 overflow-x-hidden overflow-y-auto md:inset-0 h-[calc(100%-1rem)] backdrop-blur-sm"
      }
    >
      <div className="relative w-full max-w-2xl max-h-full ">
        {/* <!-- Modal content --> */}
        <div className="relative bg-white rounded-lg shadow dark:bg-gray-700">
          {/* <!-- Modal header --> */}
          <div className="flex items-start justify-between p-4 border-b rounded-t dark:border-gray-600">
            <h3 className="text-xl font-semibold text-gray-900 dark:text-white">
              {isRoleEditable ? "Update Role" : "Add New Role"}
            </h3>
            <button onClick={onClose} className="btn btn-warning">
              Close model
            </button>
          </div>
          {/* <!-- Modal body --> */}
          <form
            className="bg-white shadow-md rounded px-8 pt-6 pb-8 mb-4"
            onSubmit={formik.handleSubmit}
          >
            <div className="mb-4">
              <TextInput
                placeholder={"Role Name"}
                name="role"
                onChange={formik.handleChange}
                value={formik.values.role}
                error={formik.errors.role}
              />
            </div>
            <button type="submit" className="btn btn-primary m-1">
              Yes
            </button>
            <button onClick={onClose} className="btn btn-warning m-1">
              No
            </button>
          </form>
        </div>
      </div>
    </div>
  );
};

export default AddEditRole;
